.Form {
    align-items: center;
    padding-top: 5px;
}

.selectDate {
    position: absolute;
    width: 100%;

    &.openSelect {
        transition: bottom 1s ease;
        bottom: 0;
    }

    &.closeSelect {
        transition: bottom 1s ease;
        bottom: -300px;

    }
}

.selectTime {


    &.openSelect {
        transition: left 1s ease;
        left: 0;
    }

    &.closeSelect {
        transition: left 1s ease;
        left: -1300px;

    }
}

.groupBattonTime {
    scrollbar-width: none; /* Скрыть полосы прокрутки в Firefox */
    -ms-overflow-style: none;
    overflow: scroll;
    //height: 27vh;
    display: flex;
