.Slider {
    bottom: 20px;
    width: 100vw;
    height: 100px;
    overflow-y: hidden;
    display: flex;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */

    /* Добавить скрытые полосы прокрутки (чтобы они не занимали место) */
    &::-webkit-scrollbar {
        display: none; /* Safari and Chrome */
    }
}

.wrapperElement {
    display: flex;
}

.icon {
    position: absolute;
    right: 10px;
    top: 10px;
    width: 55px;
    height: auto;
    margin: 5px 0 0 10px
}

.textHeader {
    margin-left: 7px;
    opacity: 0.9;
    width: 55px;
    overflow-wrap: normal;
}

.gradient {
    position: relative;
    border-radius: 10px;
    width: 170px;
    margin: 5px;
    height: 90px;
    font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
    font-weight: 800;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

    font-size: 12px;
    background-image: linear-gradient(to right, #41a4d9, #ffffff);
    background-size: cover;

}