.Widget {
    display: flex;
    justify-content: center;
    width: 100vw;
}

.card {
    position: relative;
    margin: 0 0 10px 0;
    //border-radius: 10px;
    width: 100vw;
    height: 23vh;
    font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
    font-weight: 500;
    line-height: 1.5;
    letter-spacing: 0.5em;
    color: white;
    font-size: 15px;
    background-image: url("../../icons/image/header.png");
    //background-image: linear-gradient(to right, #020267, #41a4d9);
    background-size: cover;


}

.text {
    position: absolute;
    left: 20px;
    top: 10px;
}

.number {
    position: absolute;
    left: 20px;
    bottom: 20px;

}